
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Card from 'vux/src/components/card/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
  components: {
    Group,
    Selector,
    Card,
    XInput,
    XButton
  },
  data () {
    return {
      name: '',
      mobile: '',
      defaultValue: '',
      plainList: ['', ''],
      defaultIntro: {
        title: '',
        detail: '-POS'
      },
      intro: [{
        title: '',
        detail: '-POS'
      }, {
        title: '',
        detail: '-POS'
      }]
    }
  },
  methods: {
    onChange (val) {
      if (val === '') {
        this.defaultIntro = this.intro[0]
      } else {
        this.defaultIntro = this.intro[1]
      }
    },
    submitApply () {
      console.log(this.name)
      console.log(this.mobile)
      if (!this.name) {
        alert('')
        return
      }
      if (!this.mobile) {
        alert('')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 1, apply_type: this.defaultValue, name: this.name, mobile: this.mobile } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}




