
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
  components: {
    Group,
    Selector,
    XInput,
    XButton,
    Checklist,
    Cell
  },
  data () {
    return {
      name: '',
      mobile: '',
      quota: '',
      show: false,
      assetList: [ '', '', '', '' ],
      chooseAsset: [],
      bankList: [ '', '', '', '', '', '', '', '' ],
      chooseBank: [],
      defaultValue: '',
      plainList: ['', '']
    }
  },
  methods: {
    onChange (val) {
      if (val === '') {
        this.show = true
      } else {
        this.show = false
      }
    },
    submitApply () {
      console.log(this.name)
      console.log(this.mobile)
      if (!this.name) {
        alert('')
        return
      }
      if (!this.mobile) {
        alert('')
        return
      }
      if (!this.bankList) {
        alert('')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 2, apply_type: this.bankList[0] + '...', name: this.name, mobile: this.mobile, credit_card: this.defaultValue, quota: this.quota, assets: this.chooseAsset, banks: this.chooseBank } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



