
import axios from 'axios'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
  components: {
    Group,
    XInput,
    XButton,
    Checklist,
    Cell,
    Selector
  },
  data () {
    return {
      show: false,
      name: '',
      mobile: '',
      sum: '',
      joinList: [ '', '' ],
      joinValue: '',
      list: [ '', 'POS', '' ],
      chooseType: []
    }
  },
  methods: {
    chooseJoinValue (val) {
      this.workValue = val
      if (val === '') {
        this.show = true
      } else {
        this.show = false
      }
    },
    submitApply () {
      if (!this.name) {
        alert('')
        return
      }
      if (!this.mobile) {
        alert('')
        return
      }
      axios.get('http://www.zxjlaravel.com/info/submitApply', { params: { type: 4, name: this.name, mobile: this.mobile, apply_type: this.joinValue, assets: this.chooseType } })
        .then(function (response) {
          var data = response.data
          if (data.errorCode === 0) {
            location.href = '#/msg'
          } else {
            alert(data.errorInfo)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
    }
  }
}



